.App {
  text-align: center;
}

.container p, label {
    font-family: Verdana, Tahoma, "Trebuchet MS", "DejuVu Sans", "Bitstream Vera Sans", sans-serif !important;
} /* this affects every element in the body and the body itself */

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

@media (min-width: 1200px) {
  .container {
    max-width: 1140px;
  }
}

@media (min-width: 992px) and (max-width: 1199px) {
  .container {
    max-width: 960px;
  }
}

@media (min-width: 767px) and (max-width: 991px) {
  .container {
    max-width: 720px;
  }
}


@media (min-width: 576px) and (max-width: 767px) {
  .container {
    max-width: 540px;
  }
}

h2 {
  font-size: 1.25rem;
  color: #6e6e6e;
  letter-spacing: 0.5em;
  font-family: Montserrat-Regular;
}

h3 {
  font-size: 0.75rem;
  color: #6e6e6e;
  letter-spacing: 0.5em;
  font-family: Montserrat-Regular;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.carousel .slide img {
  height: 100vh;
  object-fit: cover;
  width: auto;
}

button.control-arrow:before {
  display: none !important;
}

.arrow-img {
  filter: brightness(100);
}

.leftSideMenu {
  float: right;
  display: flex;
}

.rightSideMenu {
  float: left;
  display: flex;
}

.menuItemContainer:hover {
  text-decoration: underline;
}

.arrow-down {
  transition: all .25s ease-in;
  opacity: .4;
}

.arrow-down:hover {
  opacity: 1;
}

.formInput {
  width: 100%;
    padding: 12px;
    margin: 6px 0 4px;
    border: 1px solid #ccc;
    background: #fafafa;
    color: #000;
    font-family: sans-serif;
    font-size: 12px;
    line-height: normal;
    box-sizing: border-box;
    border-radius: 2px;
}

input, label {
  display: block;
}

input[type=submit] {
  color: white;
  background-color: #272727;
}

input[type=submit]:hover {
  background-color: #767676;
}